.footer {
  position: sticky;
  bottom: 0;
  background-image: url(../images/footer.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 200px;
  z-index: -1;


  @media (min-width: 744px) {
   height: 480px;
  }

  @media (min-width: 1260px) {
    height: 555px;
   }
   
}
